        ----------------SAVE BUTTON---------------
*/


.btnSave {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 20px;
    border-radius: 10px;
    border: none;
    background-color: transparent;
    position: relative;
    cursor: pointer;
}
  
.btnSave::after {
    content: 'like';
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    position: absolute;
    font-size: 15px;
    color: white;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    opacity: 0;
    visibility: hidden;
    transition: .2s linear;
    top: 115%;
}
  
.icon {
    width: 30px;
    height: 30px;
    transition: .2s linear;
}
  
.icon path {
    fill: white;
    transition: .2s linear;
}
  
.btnSave:hover > .icon {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
  
.btnSave:hover > .icon path {
    fill: #C99C6E;
}
  
.btnSave:hover::after {
    visibility: visible;
    opacity: 1;
    top: 105%;
}
  
  
